<template>
  <div class="three-container" ref="threeContainer"></div>
  <button class="debug-button" @click="toggleDebug">
    {{ debugEnabled ? "关闭调试" : "打开调试" }}
  </button>

  <!-- 对话框 -->
  <div v-if="showDialog" class="dialog-container" :style="dialogPosition">
    <div class="dialog-box">
      <p class="dialog-text">{{ displayedText }}</p>
      <button
        v-if="showPurchaseButton"
        class="purchase-button"
        @click="handleInteraction"
      >
        去看看
      </button>
    </div>
  </div>
  <div class="controls-guide" v-if="!isMobile">
    <div class="guide-title">操作指引</div>
    <div class="guide-item">
      <span class="key">W A S D</span>
      <span class="description">控制方向</span>
    </div>
    <div class="guide-item">
      <span class="key">SHIFT</span>
      <span class="description">切换跑步/走路</span>
    </div>
  </div>
  <!-- <SuccessDialog v-model:visible="show" message="支付成功" /> -->
</template>

<script setup>
import { ref, onMounted, onBeforeUnmount } from "vue";
import { MetaverseViewer } from "../infranstructure/viewers/metaverse/index.js";
// import { useRoute } from "vue-router";
import { getScreenOrientation, isWeixin, loadWxJsSdk  } from "../utils/index.js";
// 页面可见性监控变量
let lastHiddenTime = 0;
let visibilityTimeout = 10000; // 10秒

const threeContainer = ref(null);
let metaverse = null;
// const route = useRoute();
// console.log("route.query", route.query);
const modelUrl = ref('');
const debugEnabled = ref(false);
const interactionEnabled = ref(false);
const modelType = ref('');

let geometry = null;
let geometryPos = null;

// 对话框状态
const showDialog = ref(false);
let fullText = "先生~麦当劳大特惠，了解一下";
const displayedText = ref("");
const showPurchaseButton = ref(false);
const dialogPosition = ref(null);

let closeDialog = false;
let isInZoneOrigin = false;

// 文字动画相关变量
let textAnimationInterval = null;
let currentCharIndex = 0;

// 检测是否为移动设备
const isMobile = ref(true);

// 文字动画函数
const animateText = () => {
  if (currentCharIndex < fullText.length) {
    displayedText.value = fullText.substring(0, currentCharIndex + 1);
    currentCharIndex++;
  } else {
    clearInterval(textAnimationInterval);
    // 文字显示完毕后显示购买按钮
    setTimeout(() => {
      showPurchaseButton.value = true;
    }, 500);
  }
};

// 开始文字动画
const startTextAnimation = () => {
  // 重置状态
  currentCharIndex = 0;
  displayedText.value = "";
  showPurchaseButton.value = false;

  // 清除可能存在的旧定时器
  if (textAnimationInterval) {
    clearInterval(textAnimationInterval);
  }

  // 启动新的文字动画
  textAnimationInterval = setInterval(animateText, 100); // 每100毫秒显示一个字符
};

const initIeration = (isInZone, text, type) => {
  // metaverse.setInteractionCallback((isInZone, text, type) => {
    if (closeDialog && isInZone && isInZoneOrigin !== isInZone) return;
    if (isInZone === isInZoneOrigin) return;
    if (isInZone && !showDialog.value) {
      fullText = text;
      // 玩家进入区域，显示对话框
      showDialog.value = true;
      modelType.value = type;
      // 开始文字动画
      startTextAnimation();
    } else if (!isInZone && showDialog.value) {
      // 玩家离开区域，隐藏对话框
      showDialog.value = false;
    }
    isInZoneOrigin = isInZone;
    closeDialog = false;
  // });
};

// 屏蔽长按操作的事件处理函数
const preventLongPress = (event) => {
  event.preventDefault();
  event.stopPropagation();
  return false;
};

// 监控页面可见性变化
const handleVisibilityChange = () => {
  if (document.hidden) {
    // 页面切到后台时保存位置
    console.log("页面切到后台，保存当前位置");
    if (metaverse && metaverse.playerController && metaverse.sceneController) {
      metaverse.playerController.saveCurrentPosition(
        metaverse.sceneController,
        "documentVisibility",
      );
    }
  } else {
    // 页面切回来时恢复位置
    console.log("页面切回前台，恢复保存的位置");
    setTimeout(() => {
      if (
        metaverse &&
        metaverse.playerController &&
        metaverse.sceneController
      ) {
        metaverse.playerController.restoreSavedPosition(
          metaverse.sceneController,
          "documentVisibility",
          true
        );
      }
    }, 200);
  }
};

// 组件挂载时初始化Metaverse
onMounted(async () => {
  await init();
  // 添加页面可见性变化监听器
  document.addEventListener("visibilitychange", handleVisibilityChange);

  try {
    if (!modelUrl.value) return;
    metaverse = new MetaverseViewer(
      threeContainer.value,
      decodeURIComponent(modelUrl.value),
      {
        onInteractionCallback: initIeration.bind(this)
      }
    );
    await metaverse.init();
    // 初始化窗口控制器
    isMobile.value = metaverse.getIsMobile();
    metaverse.setDebugPanelVisible(debugEnabled.value);


    // 添加窗口大小调整监听器
    window.addEventListener("resize", onWindowResize);

    // 屏蔽长按操作事件监听器
    if (threeContainer.value) {
      // 禁用右键菜单
      threeContainer.value.addEventListener("contextmenu", preventLongPress);

      // 禁用长按选择
      threeContainer.value.addEventListener("selectstart", preventLongPress);

      // 禁用拖拽开始
      threeContainer.value.addEventListener("dragstart", preventLongPress);

      // 移动端长按事件处理
      let longPressTimer = null;

      threeContainer.value.addEventListener("touchstart", (e) => {
        longPressTimer = setTimeout(() => {
          e.preventDefault();
          e.stopPropagation();
        }, 500); // 500ms后触发长按阻止
      });

      threeContainer.value.addEventListener("touchend", () => {
        if (longPressTimer) {
          clearTimeout(longPressTimer);
          longPressTimer = null;
        }
      });

      threeContainer.value.addEventListener("touchmove", () => {
        if (longPressTimer) {
          clearTimeout(longPressTimer);
          longPressTimer = null;
        }
      });
    }
  } catch (error) {
    console.error("MetaverseViewer 初始化失败:", error);
  }
});

// 窗口大小调整处理
const onWindowResize = () => {
  if (metaverse) {
    metaverse.onWindowResize();
  }
};

// 组件卸载前清理资源
onBeforeUnmount(() => {
  if (metaverse) {
    metaverse.destroy();
  }

  // 移除页面可见性变化监听器
  document.removeEventListener("visibilitychange", handleVisibilityChange);
});
// 切换调试模式
const toggleDebug = () => {
  debugEnabled.value = !debugEnabled.value;
  if (metaverse) {
    metaverse.setDebugPanelVisible(debugEnabled.value);
    console.log("调试状态已切换为:", debugEnabled.value ? "开启" : "关闭");
  }
};

// 处理交互按钮点击
const handleInteraction = () => {
  console.log("交互按钮被点击", metaverse.playerController.getPosition(), 'modelType', modelType.value);

  // @ts-ignore
  window.wx.miniProgram.postMessage({
    data: {
      action: "pay",
      orderId: "12345",
      position: metaverse.playerController.getPosition(),
      modelType: modelType.value,
    },
  });
  // @ts-ignore

  // wx.miniProgram.navigateBack();
  console.log("wx.miniProgram.navigateTo");
  showDialog.value = false;
  closeDialog = true;

  // wx.miniProgram.navigateTo({url: `/pages/authorize/index`});
  wx.miniProgram.navigateTo({ url: `/pages/authorize/index?modelType=${modelType.value}` });
  // 这里可以添加交互逻辑，例如显示对话框、触发事件等
  // alert("您已触发交互事件！");
};

// 恢复保存的位置和视角信息
const restoreSavedPosition = () => {
  if (!metaverse || !metaverse.playerController || !metaverse.sceneController) {
    return;
  }

  metaverse.playerController.restoreSavedPosition(
    metaverse.sceneController,
    "default",
  );
};
const init = async () => {
  if(isWeixin()) {
    console.log('微信SDK:')
    loadWxJsSdk();
  }
  // 获取 hash 部分（去除 #）
  const hashString = window.location.href.split('#/viewer')[1];   // 或 window.location.hash.slice(1) 在实际页面上
  console.log('hashString1===', hashString);
  // 用 URLSearchParams 解析
  const params = new URLSearchParams(hashString);

  const sceneUrl = params.get('sceneUrl');
  console.log('sceneUrl====', sceneUrl);
  modelUrl.value = sceneUrl;
  // 初始化窗口控制器
  // const requestFile = await fetch(sceneUrl, {
  //   mode: 'cors',
  //   credentials: 'omit',
  //   cache: 'reload',
  // });
  // console.log(1111, requestFile);
  // if (requestFile.status !== 200) {
  //   return new Error(`load  error ${requestFile.statusText},${requestFile.body}`);
  // }
  // const content = await requestFile.json();
  // console.log('content==', content);
  // document.title = content.name;
  // if (content.interaction) {
  //   interactionEnabled.value = content.interaction.interaction;
  //   if (content.interaction.geometry && content.interaction.geometryPos) {
  //     geometry = content.interaction.geometry.split("|");
  //     geometryPos = content.interaction.geometryPos.split("|");
  //   }
  // }
};

</script>

