  /* 屏蔽长按操作 */
  -webkit-touch-callout: none; /* 禁用iOS长按弹出菜单 */
  -webkit-user-select: none; /* 禁用文本选择 */
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  /* 禁用长按上下文菜单 */
  -webkit-context-menu: none;
