    // 设置交互区域的位置（可以根据需要调整）
    // this.interactionZone.position.set(3, 0, 8);
    this.interactionZone2.position.set(...geometryPos);

    // 将交互区域添加到场景中
    this.sceneController.getScene().add(this.interactionZone2);

    // 创建一个用于检测碰撞的包围盒
    this.interactionBox2 = new THREE.Box3().setFromObject(this.interactionZone2);

    // 添加labubu模型到交互区域
    console.log('options.geometryPos', options.geometryPos);
